// DOCUMENTATION
// ===============
.documentation {
  padding-top: 75px;
  padding-bottom: 45px;


  .main__title {
    margin-bottom: 45px; }


  &__row {
    text-align: center; }


  &__list {
    display: flex;
    flex-wrap: wrap;
    text-align: left;

    &-item {
      margin-bottom: 30px;
      padding-right: 30px;
      @include rmin($mobile-s) {
        width: calc(100% / 1); }
      @include rmin($mobile) {
        width: calc(100% / 2); }
      @include rmin($desktop) {
        width: calc(100% / 3); }
      @include rmin($wide) {
        width: calc(100% / 4); }

      &:hover {
        i {
          opacity: 0.75;
          @include transition(opacity); }
        p {
          color: $colorIrisBlue;
          text-shadow: 0 0 20px rgba(0, 0, 0, 0.05);
          @include transition(text-shadow, color); } } }


    &-btn {
      display: flex;

      div {
        width: 32px;
        padding-right: 10px;

        i {
          display: block;
          @include transition(opacity); } }


      p {
        font-size: 14px;
        color: $colorNero;
        @include transition(text-shadow, color); } } }


  &__button {
    display: inline-block;
    margin-top: 45px;
    margin-left: auto;
    margin-right: auto;
    min-width: 190px;
    height: 45px;
    line-height: 45px;
    border-radius: 2em;
    border: 2px solid $colorGold;
    background-color: transparent;
    text-align: center;
    font-size: 15px;
    font-weight: 500;
    text-transform: uppercase;
    @include transition(background-color);

    &:hover {
      background-color: $colorGold;
      @include transition(background-color); } } }

