  width: 33px;
  margin-right: 12px;
}

.checkbox__icon span {
  display: block;
  width: 100%;
  height: 15px;
  border-radius: 2em;
  background-color: #edeff2;
}

.checkbox__icon span:before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 15px;
  height: 15px;
  border-radius: 100%;
  background-color: #666;
  transition: background-color 0.3s ease-in-out, left 0.3s ease-in-out;
