// BANNER
// ===============
.banner {
  position: relative;
  @include rmin($mobile-s) {
    min-height: 800px; }
  @include rmin($tablet) {
    min-height: 900px; }

  &--inner {
    @include rmin($mobile-s) {
      min-height: 655px; }
    @include rmin($tablet) {
      min-height: 885px; }

    .container.container--slider {
      @include rmin($mobile-s) {
        margin-top: 130px; }
      @include rmin($tablet) {
        margin-top: 260px; } } }


  &__gradient {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 250px;
    background: -moz-linear-gradient(top, rgba(0,0,0,0.5) 0%, rgba(0,0,0,0) 100%);
    background: -webkit-linear-gradient(top, rgba(0,0,0,0.5) 0%,rgba(0,0,0,0) 100%);
    background: linear-gradient(to bottom, rgba(0,0,0,0.5) 0%,rgba(0,0,0,0) 100%);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#80000000', endColorstr='#00000000',GradientType=0 ); }


  .container {
    &--slider {
      @include rmin($mobile-s) {
        margin-top: 130px; }
      @include rmin($tablet) {
        margin-top: 245px; } }

    &--swap {
      @include rmin($mobile-s) {
        margin-top: 87px; }
      @include rmin($tablet) {
        margin-top: 85px; } } }


  &__carousel {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    &--blur,
    &--normal {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%; }

    &--blur {
      height: 100%;
      .banner__carousel-image {
        filter: url(#svg-blur); } }

    &--normal {
      @include rmin($mobile-s) {
        height: calc(100% - 150px); }
      @include rmin($tablet) {
        height: calc(100% - 105px); } }

    .slick-slider,
    .slick-list,
    .slick-track {
      height: 100%; }

    &-image {
      width: 100%;
      height: 100%;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover; } }


  &__slider {
    margin-left: auto;
    margin-right: auto;
    color: $colorWhite;

    &-badge {
      display: none;
      margin-bottom: 15px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 2em;
      padding: 8px 12px;
      background-color: $colorVioletBlue;
      font-size: 10px;
      font-weight: bold;
      text-transform: uppercase;
      @include rmin($mobile-s) {
        display: inline-block; }
      @include rmin($desktop) {
        display: none; }

      .slick-center.slick-current & {
        display: inline-block;
        animation-delay: 0.3s;
        animation-name: slideInDown;
        animation-fill-mode: backwards; } }

    &-name {
      text-shadow: 0 9px 90px rgba(0, 0, 0, 0.9);
      @include rmin($mobile-s) {
        font-size: 35px;
        font-weight: 800; }
      @include rmin($mobile) {
        font-size: 50px; }
      @include rmin($tablet) {
        font-size: 55px; }
      @include rmin($desktop) {
        font-size: 14px;
        font-weight: 500; }

      .slick-prev &,
      .slick-next & {
        @include rmin($desktop) {
          font-size: 18px;
          font-weight: 500; } }

      .slick-center.slick-current & {
        @include rmin($desktop) {
          font-size: 65px;
          font-weight: 800; }

        > span {
          display: block;
          line-height: 1; } }

      .banner__slider--quarter &,
      .banner__slider--house &,
      .banner__slider--flat &,
      .banner__slider--realty & {
        font-weight: 800;
        @include rmin($mobile-s) {
          font-size: 35px; }
        @include rmin($mobile) {
          font-size: 50px; }
        @include rmin($tablet) {
          font-size: 55px; }
        @include rmin($desktop) {
          font-size: 69px; } }

      .banner__slider--car &,
      .banner__slider--pantry & {
        font-size: 20px;
        font-weight: normal;
        text-shadow: 0 9px 90px rgba($colorBlack, 0.4); } }

    &-info {
      display: none;
      margin-top: 15px;
      text-shadow: 0 9px 90px rgba(0, 0, 0, 0.9);
      font-size: 20px;
      @include rmin($mobile-s) {
        display: block; }
      @include rmin($desktop) {
        display: none; }

      .banner__slider--quarter &,
      .banner__slider--flat & {
        display: block; }

      .banner__slider--car &,
      .banner__slider--pantry & {
        display: none !important; }

      .slick-center.slick-current & {
        display: block; } }

    .slick {
      &-btn {
        cursor: pointer;
        position: absolute;
        top: 0;
        height: 100%;
        background-color: transparent;
        border: none;

        &.slick-prev {
          left: 0; }
        &.slick-next {
          right: 0; } }

      &-list {
        padding: 0 !important;
        margin: 0 50px;

        &.fadeOut {
          opacity: 0; } }


      &-slider {
        display: flex;
        align-items: center;
        min-height: 280px; }

      &-track {
        display: flex;
        align-items: center; }

      &-slide {
        text-align: center;
        font-weight: 500;
        @include rmin($mobile-s) {
          font-size: 35px; }
        @include rmin($mobile) {
          font-size: 50px; }
        @include rmin($tablet) {
          font-size: 55px; }
        @include rmin($desktop) {
          width: calc((1070px - (430px + 400px)) / 2) !important;
          font-size: 16px;

          .banner--inner & {
            width: 100% !important;
            padding-top: 30px;
            padding-bottom: 30px; } }


        &.slick-prev,
        &.slick-next {
          @include rmin($desktop) {
            width: 200px !important; } } }

      &-center.slick-current {
        width: 430px !important; } } }

  &__swap {
    &-content {
      color: $colorWhite;
      text-align: center; }

    &-title {
      margin-bottom: 20px;
      text-shadow: 0 0 13px rgba(0, 0, 0, 0.9);
      font-size: 24px;
      font-weight: bold; }

    &-wrap {
      display: flex;
      align-items: center;
      justify-content: space-between;
      max-width: 570px;
      margin-left: auto;
      margin-right: auto;
      font-size: 20px;
      font-weight: 500;
      @include rmin($mobile-s) {
        display: block; }
      @include rmin($tablet) {
        display: flex; }

      .banner__swap--storerooms & {
        max-width: 370px; }

      .banner__swap--earth & {
        max-width: 270px; }

      .banner__swap--parking & {
        max-width: 350px; }

      &--left,
      &--right {
        display: flex;
        justify-content: center; }


      p {
        text-shadow: 0 0 13px rgba(0, 0, 0, 0.9);

        .banner__swap--commercial & {
          &:first-child {
            position: relative;
            display: flex;
            align-items: center;
            margin-right: 30px;

            &:before {
              content: "";
              position: absolute;
              right: -15px;
              top: 0;
              width: 2px;
              height: 100%;
              background-color: $colorWhite; } }

          &:last-child {
            text-align: left;
            span {
              display: block; } } } }

      span {
        color: $colorGold;
        font-weight: bold; } }

    &-btn {
      display: inline-block;
      height: 45px;
      line-height: 45px;
      margin-top: 40px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 25px;
      padding-right: 25px;
      border-radius: 2em;
      border: 1px solid transparent;
      background-color: $colorGold;
      color: $colorBlack;
      font-size: 15px;
      font-weight: 500;
      text-transform: uppercase;
      @include transition(background-color, border-color);
      &:hover {
        border-color: $colorBlack;
        background-color: $colorWhite;
        @include transition(background-color, border-color); } }

    .slick {
      &-dots {
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        left: 0;
        bottom: -50px;
        width: 100%;
        margin-left: auto;
        margin-right: auto;

        li {
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          width: 30px;
          height: 15px;
          margin: {
            left: 7.5px;
            right: 7.5px; }
          &:hover {
            span {
              background-color: $colorWhite;
              border-top: 1px solid $colorWhite;
              @include transition(background-color, border-top); } }

          span {
            display: block;
            width: 100%;
            height: 1px;
            background-color: rgba($colorWhite, 0.5);
            border-top: 1px solid transparent;
            @include transition(background-color, border-top); }


          &.slick-active {
            span {
              background-color: $colorWhite;
              border-top: 1px solid $colorWhite;
              @include transition(background-color, border-top); } } } } } } }
