// TAB
// ===============
.tab {
  display: none;
  @include r($mobile-max) {
    overflow-y: auto;
    z-index: 1000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    min-height: 100%;
    background-color: $colorWhite; }
  @include rmin($tablet) {
    display: block; }

  .preloader & {
    display: none; }

  &.is-show {
    @include r($mobile-max) {
      display: block;
      animation-name: wowFade;
      visibility: visible; } }

  &__header {
    @include rmin($mobile-s) {
      display: flex;
      justify-content: space-between;
      padding-top: 15px;
      height: 160px; }
    @include rmin($tablet) {
      display: none; }

    &-wrap {
      display: flex;
      align-items: center; }

    &--left,
    &--right {
      display: flex;
      width: 50%; }

    &--right {
      justify-content: flex-end; } }

  &__footer {
    @include rmin($mobile-s) {
      display: block; }
    @include rmin($tablet) {
      display: none; } }

  &__list {
    justify-content: center;
    @include rmin($mobile-s) {
      display: block; }
    @include rmin($tablet) {
      display: flex; } }


  &__item {
    position: relative;

    &:after {
      content: "";
      z-index: 2;
      position: absolute;
      border-radius: 0;
      width: 0;
      height: 0;
      //+transition(width, height, border-radius, background-color, box-shadow)
      @include r($mobile-max) {
        top: 0;
        left: 0; }
      @include rmin($tablet) {
        top: -2.5px;
        left: -2.5px; }
      @include rmin($desktop) {
        top: -5px;
        left: -5px; } }

    &.is-active {
      &:after {
        //+transition(width, height, border-radius, background-color, box-shadow)
        border-radius: 5px;
        @include r($mobile-max) {
          width: 100%;
          height: 100%;
          border-radius: 0; }
        @include rmin($tablet) {
          width: calc(100% + 5px);
          height: calc(100% + 5px); }
        @include rmin($desktop) {
          width: calc(100% + 10px);
          height: calc(100% + 10px); } }

      .tab__link {
        z-index: 3; } }


    &-0,
    &-1,
    &-2,
    &-3,
    &-4 {
      &:before {
        content: "";
        z-index: 0;
        position: absolute;
        top: 0;
        @include r($mobile-max) {
          box-shadow: none !important; }
        @include rmin($tablet) {
          height: 48px; }
        @include rmin($desktop) {
          height: 60px; } }

      &.is-active {
        &:after {
          //+transition(background-color, box-shadow)
          @include r($mobile-max) {
            box-shadow: none !important; } } } }

    &-0 {
      &:before {
        left: 10px;
        width: 100%;
        box-shadow: 0 15px 50px 0 rgba($colorEmerald, 0.6); }
      &.is-active {
        &:after {
          background-color: $colorEmerald;
          box-shadow: 0 0 20px 5px rgba($colorEmerald, 0.55); } } }
    &-1 {
      &:before {
        left: 10px;
        width: calc(100% - 10px);
        box-shadow: 0 15px 50px 0 rgba($colorGold, 0.6); }
      &.is-active {
        &:after {
          background-color: $colorGold;
          box-shadow: 0 0 20px 5px rgba($colorGold, 0.55); } } }
    &-2 {
      &:before {
        left: 0;
        width: calc(100% - 10px);
        box-shadow: 0 15px 50px 0 rgba($colorBlizzardBlue, 0.6); }
      &.is-active {
        &:after {
          background-color: $colorBlizzardBlue;
          box-shadow: 0 0 20px 5px rgba($colorBlizzardBlue, 0.55); } } }
    &-3 {
      &:before {
        right: 10px;
        width: 100%;
        box-shadow: 0 15px 50px 0 rgba($colorLightningYellow, 0.6); }
      &.is-active {
        &:after {
          background-color: $colorLightningYellow;
          box-shadow: 0 0 20px 5px rgba($colorLightningYellow, 0.55); } } }
    &-4 {
      &:before {
        right: 10px;
        width: 100%;
        box-shadow: 0 15px 50px 0 rgba($colorDeepCerise, 0.6); }
      &.is-active {
        &:after {
          background-color: $colorDeepCerise;
          box-shadow: 0 0 20px 5px rgba($colorDeepCerise, 0.55); } } } }


  &__link {
    z-index: 1;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $colorWhite;
    font-weight: bold;
    @include r($mobile-max) {
      height: 60px;
      padding-left: 15px;
      justify-content: flex-start; }
    @include rmin($tablet) {
      height: 48px;
      padding-left: 20px;
      padding-right: 20px;
      font-size: 12px; }
    @include rmin($desktop) {
      height: 60px;
      padding-left: 30px;
      padding-right: 30px;
      font-size: 15px; }

    &--emerald {
      background-color: $colorEmerald;
      @include transition(box-shadow);
      &:hover {
        box-shadow: 0 0 15px 3px rgba($colorEmerald, 0.55);
        @include transition(box-shadow); } }
    &--gold {
      background-color: $colorGold;
      @include transition(box-shadow);
      &:hover {
        box-shadow: 0 0 15px 3px rgba($colorGold, 0.55);
        @include transition(box-shadow); } }
    &--blizzardBlue {
      background-color: $colorBlizzardBlue;
      @include transition(box-shadow);
      &:hover {
        box-shadow: 0 0 15px 3px rgba($colorBlizzardBlue, 0.55);
        @include transition(box-shadow); } }
    &--lightningYellow {
      background-color: $colorLightningYellow;
      @include transition(box-shadow);
      &:hover {
        box-shadow: 0 0 15px 3px rgba($colorLightningYellow, 0.55);
        @include transition(box-shadow); } }
    &--deepCerise {
      background-color: $colorDeepCerise;
      @include transition(box-shadow);
      &:hover {
        box-shadow: 0 0 15px 3px rgba($colorDeepCerise, 0.55);
        @include transition(box-shadow); } } } }

