// ABOUT
// ===============
.about {
  padding-top: 15px;
  padding-bottom: 30px;


  &--car,
  &--pantry,
  &--house {
    position: relative;
    display: flex;
    align-items: flex-end;
    min-height: 405px;
    padding-top: 0;
    padding-bottom: 0;
    padding-left: 15px;
    padding-right: 15px;

    img {
      z-index: 0;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center; }

    .container {
      z-index: 1;
      position: relative;
      max-width: 960px;
      background-color: rgba($colorWhite, 0.9);
      @include rmin($mobile-s) {
        padding-top: 30px;
        padding-bottom: 30px; }
      @include rmin($wide) {
        padding-top: 45px;
        padding-bottom: 45px; }

      h2 {
        font-weight: 800;
        text-align: center;
        @include rmin($mobile-s) {
          margin-bottom: 15px;
          font-size: 30px; }
        @include rmin($mobile) {
          margin-bottom: 30px;
          font-size: 35px; }
        @include rmin($tablet) {
          margin-bottom: 35px;
          font-size: 40px; }
        @include rmin($desktop) {
          margin-bottom: 40px;
          font-size: 45px; }
        @include rmin($wide) {
          margin-bottom: 45px;
          font-size: 50px; } }

      p {
        color: $colorDimGray;
        text-align: center;
        line-height: 1.4;
        @include rmin($mobile-s) {
          font-size: 16px; }
        @include rmin($mobile) {
          font-size: 18px; }
        @include rmin($desktop) {
          font-size: 20px; } } } }

  &--car,
  &--pantry {
    min-height: 315px;
    margin-top: 45px;
    margin-bottom: 45px;

    &:after {
      content: "";
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      background: -moz-linear-gradient(top, rgba(255,255,255,1) 0%, rgba(255,255,255,0) 100%);
      background: -webkit-linear-gradient(top, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
      background: linear-gradient(to bottom, rgba(255,255,255,1) 0%,rgba(255,255,255,0) 100%);
      filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#ffffff', endColorstr='#00ffffff',GradientType=0 ); }

    a {
      cursor: pointer;
      display: block;
      width: 100%;
      max-width: 260px;
      height: 45px;
      line-height: 40px;
      margin-top: 45px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 15px;
      padding-right: 15px;
      border: 2px solid $colorGold;
      border-radius: 2em;
      background-color: $colorGold;
      font-size: 15px;
      font-weight: 500;
      text-transform: uppercase;
      text-align: center;
      @include transition(background-color);
      &:hover {
        background-color: transparent;
        @include transition(background-color); } } }


  .main__title {
    @include rmin($mobile-s) {
      margin-bottom: 15px; }
    @include rmin($tablet) {
      margin-bottom: 30px; }
    @include rmin($desktop) {
      margin-bottom: 45px; } }


  &__row {
    display: flex;
    @include rmin($mobile-s) {
      flex-direction: column; }
    @include rmin($desktop) {
      flex-direction: row; } }



  &__left {
    padding-top: 30px;
    text-align: center;
    @include rmin($mobile-s) {
      width: 100%;
      margin-bottom: 30px; }
    @include rmin($desktop) {
      width: 400px; } }


  &__right {
    @include rmin($mobile-s) {
      width: 100%;
      text-align: center; }
    @include rmin($desktop) {
      width: calc(100% - 400px);
      text-align: left; }

    p {
      font-size: 16px;
      line-height: 1.9; } } }
