// SUBMENU
// ===============
.submenu {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  @include rmin($mobile-s) {
    min-height: 150px; }
  @include rmin($tablet) {
    min-height: 105px; }

  svg {
    z-index: 0;
    position: absolute;
    left: -9999px;
    top: -9999px; }


  &__list {
    display: flex;
    align-items: center;
    min-height: 105px;
    @include rmin($mobile-s) {
      flex-wrap: wrap;
      justify-content: space-between; }
    @include rmin($tablet) {
      flex-wrap: nowrap;
      justify-content: space-around; }

    .media & {
      justify-content: flex-start; } }


  &__item {
    @include rmin($mobile-s) {
      display: flex;
      align-items: center;
      height: 75px; }
    @include rmin($tablet) {
      height: auto;
      display: block; }

    .submenu__list--flat &,
    .submenu__list--quarter & {
      &:first-child {
        @include rmin($mobile-s) {
          width: 50%; }
        @include rmin($tablet) {
          width: auto; }

        .submenu__btn {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 45px;
          line-height: 45px;
          border: 2px solid $colorGold;
          border-radius: 2em;
          text-transform: uppercase;
          @include transition(background-color, color);
          @include rmin($mobile-s) {
            width: 145px;
            font-size: 11px; }
          @include rmin($tablet) {
            width: 160px;
            font-size: 13px; }
          @include rmin($desktop) {
            width: 180px;
            font-size: 14px; }
          @include rmin($wide) {
            width: 200px;
            font-size: 15px; }

          &:hover {
            background-color: $colorGold;
            color: #000;
            @include transition(background-color, color); } } }

      &:nth-child(2) {
        @include rmin($mobile-s) {
          justify-content: flex-end;
          width: 50%; }
        @include rmin($tablet) {
          justify-content: flex-start;
          width: auto; } } }

    .submenu__list--car &,
    .submenu__list--pantry &,
    .submenu__list--gallery & {
      &:first-child, {
        .submenu__btn {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 45px;
          line-height: 45px;
          white-space: nowrap;
          text-transform: uppercase;
          @include rmin($mobile-s) {
            width: 145px;
            font-size: 11px; }
          @include rmin($mobile) {
            width: 160px;
            font-size: 13px; }
          @include rmin($desktop) {
            width: 180px;
            font-size: 14px; }
          @include rmin($wide) {
            width: 200px;
            font-size: 15px; } } }

      &:first-child {
        @include rmin($mobile-s) {
          order: 0;
          width: 50%; }
        @include rmin($tablet) {
          order: 0;
          width: auto; }

        .submenu__btn {
          &:before {
            content: "";
            display: inline-block;
            margin-right: 10px;
            @include sprite(submenuArrow); } } } }

    .submenu__list--house & {
      &:first-child,
      &:last-child {
        .submenu__btn {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 45px;
          line-height: 45px;
          text-transform: uppercase;
          @include rmin($mobile-s) {
            width: 145px;
            font-size: 11px; }
          @include rmin($mobile) {
            width: 160px;
            font-size: 13px; }
          @include rmin($desktop) {
            width: 180px;
            font-size: 14px; }
          @include rmin($wide) {
            width: 200px;
            font-size: 15px; } } }

      &:first-child {
        @include rmin($mobile-s) {
          order: 0;
          width: 50%; }
        @include rmin($tablet) {
          order: 0;
          width: auto; }

        .submenu__btn {
          &:before {
            content: "";
            display: inline-block;
            margin-right: 10px;
            @include sprite(submenuArrow); } } }


      &:last-child {
        @include rmin($mobile-s) {
          order: 1;
          width: 50%;
          justify-content: flex-end; }
        @include rmin($tablet) {
          order: 4;
          width: auto;
          justify-content: flex-start; }

        .submenu__btn {
          border: 2px solid $colorGold;
          border-radius: 2em;
          @include transition(background-color, color);

          &:hover {
            background-color: $colorGold;
            color: #000;
            @include transition(background-color, color); } } }

      &:nth-child(2) {
        @include rmin($mobile-s) {
          order: 2; }
        @include rmin($tablet) {
          order: 1; } }
      &:nth-child(3) {
        @include rmin($mobile-s) {
          order: 3; }
        @include rmin($tablet) {
          order: 2; } }
      &:nth-child(4) {
        @include rmin($mobile-s) {
          order: 4; }
        @include rmin($tablet) {
          order: 3; } } } }


  &__btn {
    position: relative;
    display: block;
    color: $colorWhite;
    font-weight: 500;
    @include transition(text-shadow);
    &:hover {
      text-shadow: 0 0 20px rgba($colorWhite, 0.75);
      @include transition(text-shadow); }

    @include rmin($mobile-s) {
      font-size: 10px; }
    @include rmin($mobile) {
      font-size: 16px; }
    @include rmin($desktop) {
      font-size: 18px; }
    @include rmin($wide) {
      font-size: 20px; }

    &:after {
      content: "";
      position: absolute;
      bottom: -10px;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: transparent;
      @include transition(background-color); }

    &.is-active {
      &:after {
        content: "";
        position: absolute;
        bottom: -10px;
        left: 0;
        width: 100%;
        height: 2px;
        background-color: $colorFern;
        @include transition(background-color); } } } }

