// MAIN
// ===============
.main {
  min-height: 420px;
  padding-top: 52px;
  padding-bottom: 47px;


  &__wrapper {
    h1,
    > p {
      text-align: center; }

    h1 {
      margin-bottom: 15px;
      font-weight: 800;
      @include rmin($mobile-s) {
        font-size: 30px; }
      @include rmin($mobile) {
        font-size: 40px; } }

    > p {
      width: 100%;
      margin-bottom: 53px;
      margin-left: auto;
      margin-right: auto;
      color: $colorDimGray;
      line-height: 1.45;
      @include rmin($wide) {
        width: 92%; } } }


  &__container {
    display: flex;
    @include rmin($mobile-s) {
      flex-direction: column; }
    @include rmin($mobile) {
      flex-direction: row;
      flex-wrap: wrap; }
    @include rmin($desktop) {
      flex-wrap: nowrap; } }


  &__row {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 270px;
    @include rmin($mobile-s) {
      width: 100%;
      max-width: 100%; }
    @include rmin($mobile) {
      width: 50%;
      max-width: 50%; }
    @include rmin($desktop) {
      width: 100%;
      max-width: 270px; }

    h4 {
      font-size: 0;
      line-height: 1;

      span {
        display: inline-block;
        vertical-align: middle;
        margin-right: -3px; } }

    &-0,
    &-1,
    &-2,
    &-3 {
      h4 {
        margin-bottom: 19px;

        span {
          font-weight: 800;
          &:first-child {
            font-size: 60px;
            @include rmin($desktop) {
              font-size: 50px; }
            @include rmin($wide) {
              font-size: 60px; } } } } }

    &-1,
    &-2 {
      margin-left: 15px;
      margin-right: 15px;
      @include rmin($mobile-s) {
        margin-left: 0;
        margin-right: 0;
        margin-bottom: 30px; }
      @include rmin($mobile) {
        margin-bottom: 0; }
      @include rmin($desktop) {
        margin-left: 15px;
        margin-right: 15px; }

      h4 {
        span {
          &:first-child {
            position: relative;
            left: 10px; }
          &:nth-child(2) {
            transform: rotate(-90deg);
            font-size: 21px; }
          &:last-child {
            position: relative;
            right: 8px;
            font-size: 60px;
            @include rmin($desktop) {
              font-size: 50px; }
            @include rmin($wide) {
              font-size: 60px; }
            sup {
              font-size: 35px;
              @include rmin($desktop) {
                font-size: 25px; }
              @include rmin($wide) {
                font-size: 35px; } } } } } }

    &-0 {
      margin-right: 15px;
      @include rmin($mobile-s) {
        margin-right: 0;
        margin-bottom: 30px; }
      @include rmin($mobile) {
        margin-bottom: 15px; }
      @include rmin($desktop) {
        margin-right: 15px; }

      h4 {
        color: $colorNavyBlue;

        span {
          &:first-child {
            position: relative;
            left: 10px; }
          &:last-child {
            transform: rotate(-90deg);
            font-size: 25px;
            @include rmin($desktop) {
              font-size: 21px; }
            @include rmin($wide) {
              font-size: 25px; } } } } }

    &-1 {
      @include rmin($mobile-s) {
        margin-bottom: 30px; }
      @include rmin($mobile) {
        margin-bottom: 15px; }
      @include rmin($tablet) {
        margin-bottom: 0; }

      h4 {
        color: $colorDarkTangerine; } }

    &-2 {
      h4 {
        color: $colorFern; } }

    &-3 {
      margin-left: 15px;
      @include rmin($mobile-s) {
        margin-left: 0; }
      @include rmin($desktop) {
        margin-left: 15px; }

      h4 {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 0;
        color: $colorCardinal;
        @include rmin($desktop) {
          margin-bottom: 0; }
        @include rmin($wide) {
          margin-bottom: 8px; }


        span {
          &:last-child {
            position: relative;
            top: -10px;
            font-size: 21px; } } } }

    p {
      text-align: center;
      color: $colorMatterhorn;
      font: {
        size: 15px;
        weight: 500; } } } }
