// CHOOSE
// ===============
.choose {
  padding-top: 30px;
  padding-bottom: 30px;

  .house &,
  .realty & {
    padding-top: 72px;
    padding-bottom: 50px; }


  .main__title {
    margin-bottom: 45px; }


  &__row {
    display: flex;
    @include rmin($mobile-s) {
      flex-direction: column; }
    @include rmin($desktop) {
      flex-direction: row; }


    &-left {
      @include rmin($tablet);
      @include rmin($desktop) {
        width: 55%; }
      @include rmin($wide) {
        width: 100%;
        max-width: 700px;
        padding-right: 50px; } }
    &-right {
      @include rmin($tablet);
      @include rmin($desktop) {
        width: 45%; }
      @include rmin($wide) {
        width: calc(100% - (700px)); } }

    &-filter {
      @include rmin($tablet) {
        padding-right: 50px;
        padding-left: 10px; } } }

  &__field {
    margin-bottom: 15px;

    &-select {
      &Wrap {
        display: flex;
        justify-content: space-between;
        @include rmin($mobile-s) {
          flex-direction: column; }
        @include rmin($mobile) {
          flex-direction: row; } }
      &House {
        .select {
          min-width: 100px; } }
      &Section {
        .select {
          min-width: 135px; } } }


    &-select,
    &-radio {
      display: flex;
      @include rmin($mobile-s) {
        align-items: center; }
      @include rmin($mobile); }

    &-radio {
      margin-bottom: 17px;
      @include rmin($mobile-s) {
        flex-direction: column;
        align-items: flex-start; }
      @include rmin($mobile) {
        flex-direction: row;
        align-items: center; }

      &Wrap {
        display: flex;
        align-items: center;
        justify-content: center;
        padding-top: 30px;
        padding-bottom: 15px;

        > p {
          text-transform: uppercase;
          font-size: 20px;
          font-weight: bold;
          &:first-child {
            margin-right: 15px; }
          &:last-child {
            margin-left: 15px; } }

        .checkbox__icon {
          margin-right: 0; }
        .checkbox label {
          margin-bottom: 0; } }

      .choose__field-title {
        @include rmin($mobile-s) {
          width: 100%;
          margin-right: 0;
          padding-bottom: 10px; }
        @include rmin($mobile) {
          width: 180px;
          margin-right: 30px;
          padding-bottom: 0; } }


      .choose__field-wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-right: 30px;
        padding-left: 5px;
        @include rmin($mobile-s) {
          width: 100%; }
        @include rmin($mobile) {
          width: calc(100% - (180px + 30px)); } }


      button {
        cursor: pointer;
        width: 45px;
        height: 45px;
        padding: 0;
        border-radius: 100%;
        border: none;
        background-color: $colorSolitude;
        font-size: 20px;

        &.is-active {
          color: $colorWhite;
          background-color: $colorIrisBlue; } } }


    &-table {
      position: relative;

      &--sale,
      &--rent {
        display: none;

        &.is-active {
          display: block; } }

      &:after {
        content: "";
        z-index: 0;
        position: absolute;
        left: 0;
        bottom: 0;
        width: 100%;
        height: 60px;
        background: -moz-linear-gradient(top, rgba(255,255,255,0) 0%, rgba(255,255,255,1) 100%);
        background: -webkit-linear-gradient(top, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
        background: linear-gradient(to bottom, rgba(255,255,255,0) 0%,rgba(255,255,255,1) 100%);
        filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00ffffff', endColorstr='#ffffff',GradientType=0 ); }

      .table {
        display: flex;
        flex-direction: column;

        &__thead,
        &__tbody {
          @include rmin($tablet) {
            padding-right: 50px; } }

        &__tbody {
          position: relative;
          max-height: 550px;
          overflow: hidden;
          overflow-y: auto;
          padding-bottom: 60px;

          .table__tr {
            z-index: 1;
            position: relative;
            border-radius: 2em;
            @include transition(background-color, border-radius, color);
            &:after {
              opacity: 0;
              content: "";
              position: absolute;
              top: 0;
              right: 0;
              @include sprite(table-arrow);
              @include transition(opacity, right); }

            &:hover {
              background-color: $colorSolitude;
              @include transition(background-color, color); }

            &.is-active {
              border-top-right-radius: 0;
              border-bottom-right-radius: 0;
              color: $colorWhite;
              background-color: $colorIrisBlue;
              @include transition(background-color, border-radius, color);

              &:after {
                opacity: 1;
                right: -20px;
                @include transition(opacity, right); } }


            &.is-hide {
              display: none; } } }


        &__tr {
          cursor: pointer;
          display: flex;
          position: relative;
          height: 45px;
          align-items: center; }

        &__th,
        &__td {
          display: flex;
          align-items: center;
          justify-content: center;
          height: 100%;
          white-space: nowrap;
          text-align: center;
          @include rmin($mobile-s) {
            width: calc(100% / 5);
            font-size: 10px; }
          @include rmin($mobile) {
            font-size: 14px;
            &:nth-child(1) {
              width: 70px; }
            &:nth-child(2) {
              width: 110px; }
            &:nth-child(3),
            &:nth-child(4),
            &:nth-child(5) {
              width: calc((100% - (70px + 110px)) / 3); } }
          @include rmin($wide) {
            font-size: 16px; }

          .choose--car &,
          .choose--realty & {
            width: calc(100% / 4); } }

        &__th {
          font-weight: bold;

          &.is-active {
            .icon {
              transform: rotate(180deg);
              @include transition(transform, opacity); } }

          &:hover {
            .icon {
              opacity: 1; } }

          .icon {
            @include rmin($mobile) {
              margin-left: 5px;
              @include transition(transform, opacity); } } }


        &__td {
          &.is-sort {
            background-color: $colorSolitude; } } } }


    &-title {
      margin-right: 15px;
      @include rmin($mobile-s) {
        font-size: 16px; }
      @include rmin($mobile) {
        font-size: 20px; } }

    &-plan {
      margin-bottom: 27px;

      img {
        display: block;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto; } }

    &-price {
      display: flex;
      margin-left: auto;
      margin-right: auto;
      margin-bottom: 20px;
      @include rmin($mobile-s) {
        flex-direction: column;
        max-width: 470px; }
      @include rmin($mobile) {
        flex-direction: row; }


      .choose__field-left,
      .choose__field-right {
        @include rmin($mobile-s) {
          width: 100%;
          text-align: center; }
        @include rmin($mobile) {
          width: 50%;
          text-align: left; }

        p {
          &:first-child {
            font-size: 16px; }
          &:last-child {
            font-weight: bold;
            @include rmin($mobile-s) {
              font-size: 25px; }
            @include rmin($mobile) {
              font-size: 30px; } } } }

      .choose__field-left {
        @include rmin($mobile) {
          padding-left: 50px; } }

      .choose__field-right {
        @include rmin($mobile) {
          padding-left: 33px; } } }

    &-description {
      display: flex;
      align-items: center;
      justify-content: space-around;
      max-width: 470px;
      margin-left: auto;
      margin-right: auto;
      border-radius: 5px;
      padding: 30px 15px;
      margin-bottom: 37px;
      background-color: $colorSolitude;
      @include rmin($mobile-s) {
        flex-direction: column; }
      @include rmin($mobile) {
        flex-direction: row; }

      .choose__field-left,
      .choose__field-middle {
        @include rmin($mobile-s) {
          margin-bottom: 15px; }
        @include rmin($mobile) {
          margin-bottom: 0; } }


      .choose__field-left {
        text-align: center;
        font-size: 20px; }

      .choose__field-middle {
        .choose__field-wrap {
          span {
            &:last-child {
              width: 35px; } } } }

      .choose__field-right {
        .choose__field-wrap {
          span {
            &:last-child {
              width: 50px; } } } }

      .choose__field-wrap {
        margin-bottom: 15px;
        text-align: right;
        &:last-child {
          margin-bottom: 0; }

        span {
          &:last-child {
            display: inline-block;
            font-weight: bold; } } } }

    &-contacts {
      margin-bottom: 24px;
      text-align: center;

      p {
        margin-bottom: 10px;
        font-size: 20px; }

      a {
        position: relative;
        font-weight: bold;
        &:after {
          pointer-events: none;
          opacity: 0;
          content: '';
          position: absolute;
          left: 0;
          bottom: 0;
          width: 100%;
          height: 2px;
          background: $colorBlack;
          transform: translateY(5px);
          @include transition(transform, opacity); }
        &:hover {
          &:after {
            opacity: 1;
            transform: translateY(0px); } }

        @include rmin($mobile-s) {
          font-size: 25px; }
        @include rmin($mobile) {
          font-size: 30px; } } }

    &-form {
      max-width: 470px;
      margin-left: auto;
      margin-right: auto;

      .form__field {
        position: relative;
        margin-bottom: 20px;

        .error {
          position: absolute;
          left: 0;
          bottom: -15px;
          margin-bottom: 0;
          padding-left: 20px;
          color: #c7202e;
          font-size: 12px;
          font-weight: 800; }

        &--half {
          display: flex;
          @include rmin($mobile-s) {
            flex-direction: column; }
          @include rmin($mobile) {
            flex-direction: row; }

          .form__field-left {
            @include rmin($mobile-s) {
              width: 100%; }
            @include rmin($mobile) {
              width: calc(100% - (270px));
              padding-right: 15px; } }
          .form__field-right {
            @include rmin($mobile-s) {
              width: 100%;
              padding-top: 15px;
              text-align: center; }
            @include rmin($mobile) {
              width: 270px;
              padding-top: 0;
              text-align: left; }

            button.form__button {
              max-width: 270px;
              text-transform: uppercase; } } } } } } }
