// HEADER
// ===============
.header {
  z-index: 1;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;

  .preloader & {
    z-index: 1001;
    position: fixed; }


  &--cover {
    z-index: 10;
    position: relative;
    padding-top: 15px;
    padding-bottom: 25px;
    @include transition(background-color);

    .no-preloader & {
      animation-delay: 0.5s; }
    .preloader & {
      animation-delay: 9s;
      background-color: $colorWhite;
      @include transition(background-color); } }


  &__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between; }



  &__container {
    display: flex;
    align-items: center; }


  &_logo {
    &--tab {
      height: 30px; } }


  &__row {
    &-0 {
      width: 285px;
      @include rmin($mobile-s) {
        width: auto;
        padding-left: 0;
        padding-right: 30px;

        animation-name: wowFade;
        visibility: visible; }
      @include rmin($desktop) {
        padding-right: 30px;
        padding-left: 20px; }
      @include rmin($wide) {
        width: 285px; }

      .no-preloader & {
        animation-delay: 0.7s; }
      .preloader & {
        animation-delay: 9.2s; }



      svg {
        cursor: pointer;
        margin-top: 6px;
        @include rmin($mobile-s) {
          width: 90px;
          height: 30px;
          margin-top: 0; }
        @include rmin($desktop) {
          width: 179px;
          height: auto;
          margin-top: 6px; }

        .g-path-text-top,
        .g-path-text-bottom {
          path {
            @include transition(fill);
            .preloader & {
              fill: #000 !important;
              @include transition(fill); } } } } }

    &-1 {
      padding-top: 5px; }

    &-2 {
      .no-preloader & {
        animation-delay: 1.2s; }
      .preloader & {
        animation-delay: 9.7s; } }


    &-3 {
      width: 180px;
      text-align: right;
      @include rmin($mobile-s) {
        display: flex;
        width: auto;
        align-items: center; }
      @include rmin($tablet) {
        display: block;
        width: auto;
        padding-left: 15px; }
      @include rmin($desktop) {
        width: 180px; }

      .no-preloader & {
        animation-delay: 1.3s; }
      .preloader & {
        animation-delay: 9.8s; } } }


  &__phone {
    position: relative;
    top: -5px;
    color: $colorWhite;
    font-weight: 500;
    @include transition(text-shadow, color);
    &:hover {
      text-shadow: 0 0 20px rgba($colorWhite, 0.75);
      @include transition(text-shadow); }

    @include rmin($mobile-s) {
      top: 0;
      font-size: 12px; }
    @include rmin($mobile) {
      font-size: 20px; }
    @include rmin($tablet) {
      top: -5px;
      font-size: 16px; }
    @include rmin($desktop) {
      font-size: 20px; }

    .preloader & {
      color: $colorBlack;
      @include transition(color); }

    &--tab {
      @include r($mobile-max) {
        display: block;
        text-align: center;
        font-size: 30px;
        color: $colorDimGray;
        padding-top: 45px;
        padding-bottom: 45px; } } } }
