// PROGRESS
// ===============
.progress {
  padding-top: 45px;
  padding-bottom: 45px;


  .main__title {
    margin-bottom: 30px; }


  svg {
    z-index: 0;
    position: absolute;
    left: -9999px;
    top: -9999px; }


  &__row {
    display: flex;
    align-items: center;
    justify-content: center; }


  &__block {
    position: relative;
    @include rmin($mobile-s) {
      height: 255px; }
    @include rmin($mobile) {
      height: 285px; }
    @include rmin($tablet) {
      height: 315px; }
    @include rmin($desktop) {
      height: 345px; }
    @include rmin($wide) {
      height: 365px; }
    @include rmin($hd) {
      height: 395px;
      width: calc(100% / 5); }


    > div {
      width: 100%;
      height: 100%;
      background-size: cover;
      background-position: center;
      box-shadow: 0 15px 35px 0 rgba(0, 0, 0, 0.15); }

    > p {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      text-align: center;
      color: $colorWhite;
      font-weight: bold;
      @include rmin($tablet) {
        white-space: nowrap;
        font-size: 21px; }
      @include rmin($desktop) {
        font-size: 24px; }
      @include rmin($wide) {
        font-size: 27px; }
      @include rmin($hd) {
        font-size: 30px; } }


    &-0 {
      z-index: 3;
      padding-top: 20px;
      padding-bottom: 20px;
      filter: url(#svg-blur-0);
      @include rmin($mobile-s) {
        width: calc((100% / 5) - 50px); }
      @include rmin($mobile) {
        width: calc((100% / 5) - 80px); }
      @include rmin($tablet) {
        width: calc((100% / 5) - 120px); }
      @include rmin($desktop) {
        width: calc((100% / 5) - 120px); }
      @include rmin($wide) {
        width: calc((100% / 5) - 100px); } }
    &-1 {
      z-index: 4;
      padding-top: 10px;
      padding-bottom: 10px;
      @include rmin($mobile-s) {
        width: calc((100% / 5) + 30px); }
      @include rmin($mobile) {
        width: calc((100% / 5) + 40px); }
      @include rmin($tablet) {
        width: calc((100% / 5) + 80px); }
      @include rmin($desktop) {
        width: calc((100% / 5) + 60px); }
      @include rmin($wide) {
        width: calc((100% / 5) + 50px); } }
    &-2 {
      z-index: 5;
      @include rmin($mobile-s) {
        width: calc((100% / 5) + 40px); }
      @include rmin($mobile) {
        width: calc((100% / 5) + 80px); }
      @include rmin($tablet) {
        width: calc((100% / 5) + 100px); }
      @include rmin($desktop) {
        width: calc((100% / 5) + 120px); }
      @include rmin($wide) {
        width: calc((100% / 5) + 100px); } }
    &-3 {
      z-index: 4;
      padding-top: 10px;
      padding-bottom: 10px;
      @include rmin($mobile-s) {
        width: calc((100% / 5) + 30px); }
      @include rmin($mobile) {
        width: calc((100% / 5) + 40px); }
      @include rmin($tablet) {
        width: calc((100% / 5) + 80px); }
      @include rmin($desktop) {
        width: calc((100% / 5) + 60px); }
      @include rmin($wide) {
        width: calc((100% / 5) + 50px); } }
    &-4 {
      z-index: 3;
      padding-top: 20px;
      padding-bottom: 20px;
      filter: url(#svg-blur-0);
      @include rmin($mobile-s) {
        width: calc((100% / 5) - 50px); }
      @include rmin($mobile) {
        width: calc((100% / 5) - 80px); }
      @include rmin($tablet) {
        width: calc((100% / 5) - 120px); }
      @include rmin($desktop) {
        width: calc((100% / 5) - 120px); }
      @include rmin($wide) {
        width: calc((100% / 5) - 100px); } } } }
