// DESCRIPTION
// ===============
.description {
  padding-top: 30px;
  padding-bottom: 30px;


  .container {
    z-index: 0;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    height: 100%; }


  &__block {
    position: relative;
    margin-bottom: 60px;

    &-0 {
      min-height: 555px; }
    &-1 {
      min-height: 450px; }
    &-2 {
      min-height: 435px; }

    &--normal {
      .container {
        display: flex;
        justify-content: flex-end; } }

    &:last-child {
      margin-bottom: 0; }

    &-image {
      z-index: 0;
      position: absolute;
      display: flex;
      top: 0;
      height: 100%;
      @include rmin($mobile-s) {
        width: 75%; }
      @include rmin($mobile) {
        width: 65%; }
      @include rmin($tablet) {
        width: 55%; }

      .description__block--normal & {
        left: 0; }
      .description__block--reverse & {
        right: 0; }

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center; } }


    &-wrap {
      z-index: 1;
      position: relative;
      display: flex;
      height: 100%;
      @include rmin($mobile-s) {
        align-items: flex-end;
        width: 75%; }
      @include rmin($mobile) {
        align-items: center;
        width: 65%; }
      @include rmin($tablet) {
        width: 55%; } }


    &-info {
      background-color: rgba($colorWhite, 0.9);
      @include rmin($mobile-s) {
        padding-top: 15px;
        padding-bottom: 15px; }
      @include rmin($mobile) {
        padding-top: 30px;
        padding-bottom: 30px;
        padding-right: 15px; }
      @include rmin($tablet) {
        padding-right: 30px; }
      @include rmin($desktop) {
        padding-top: 45px;
        padding-bottom: 45px;
        padding-right: 65px; }

      .description__block--normal & {
        padding-right: 0;
        @include rmin($mobile-s) {
          padding-left: 15px; }
        @include rmin($tablet) {
          padding-left: 30px; }
        @include rmin($desktop) {
          padding-left: 65px; } }

      p {
        &:first-child {
          font-weight: bold;
          @include rmin($mobile-s) {
            margin-bottom: 15px;
            font-size: 25px; }
          @include rmin($tablet) {
            margin-bottom: 20px;
            font-size: 30px; }
          @include rmin($desktop) {
            margin-bottom: 30px;
            font-size: 40px; } }

        &:last-child {
          color: $colorDimGray;
          line-height: 1.5;
          @include rmin($mobile-s) {
            font-size: 14px; }
          @include rmin($tablet) {
            font-size: 15px; }
          @include rmin($desktop) {
            font-size: 20px; } } } } } }
