// MENU
// ===============
.menu {
  display: none;
  position: relative;
  top: -6px;
  left: -2px;
  @include rmin($tablet) {
    display: flex; }

  .tab & {
    @include r($mobile-max) {
      display: flex;
      align-items: center;
      justify-content: space-between;
      top: 0;
      left: 0;
      max-width: 480px;
      margin-left: auto;
      margin-right: auto;
      padding-left: 15px;
      padding-right: 15px;
      padding-bottom: 45px; } }


  &__item {
    white-space: nowrap;

    &:nth-child(1) {
      .no-preloader & {
        animation-delay: 0.8s; }
      .preloader & {
        animation-delay: 9.3s; } }
    &:nth-child(2) {
      .no-preloader & {
        animation-delay: 0.9s; }
      .preloader & {
        animation-delay: 9.4s; } }
    &:nth-child(3) {
      .no-preloader & {
        animation-delay: 1.0s; }
      .preloader & {
        animation-delay: 9.5s; } }
    &:nth-child(4) {
      .no-preloader & {
        animation-delay: 1.1s; }
      .preloader & {
        animation-delay: 9.6s; } }

    .tab & {
      @include r($mobile-max) {
        margin-right: 0;

        animation-name: wowFade;
        visibility: visible; } } }

  &__link {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    padding-left: 15px;
    padding-right: 15px;
    background-color: transparent;
    border-radius: 2em;
    color: $colorWhite;
    font-weight: 500;
    @include transition(background-color, color);
    &:hover {
      background-color: $colorWhite;
      color: $colorNero;
      @include transition(background-color, color); }

    @include rmin($tablet) {
      font-size: 11px; }
    @include rmin($desktop) {
      font-size: 12px; }

    .preloader & {
      color: $colorBlack;
      @include transition(color); }

    .tab & {
      @include r($mobile-max) {
        font-size: 12px;
        color: $colorDimGray; } } } }
